                    <div class="c">河南省  郑州市  金水区  京广快速路兴隆城交汇处东南角10号楼</div>
                  </div>
                  <div class="col-r">
                    <div class="address-default">
                      <input class="input" type="radio" name="1" /><span>设为默认地址</span>
                    </div>
                    <div class="address-bot">
                      <button class="address-bot-btn">修改</button>
                      <button class="address-bot-btn">删除</button>
                    </div>
                  </div>
                </div>
                <!--inner-address-item end-->
                <!--inner-address-item start-->
                <div class="inner-address-item">
