<script>
import Side from '@/components/side'
import Header from '@/components/header'
export default {
  data() {
    return {
      centerDialogVisible: false
    };
  },
  components:{
    Side,
    Header
  },
  methods: {

  }
}
</script>

<style scoped>
@import "../../assets/styles/address.less";
</style>



